html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  min-height: 100%;
  font-size: 18px;
}

h1,
h2,
h3 {
  margin: 0;
}

p {
  margin: 0;
  margin-bottom: 1rem;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  transition: transform .3s ease-in-out;
}

a:hover {
  transform: translateY(-.25rem);
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

// margins
// project styles
.address {
  margin: 0;
  margin-bottom: 1rem;
  padding-left: 2rem;
  font-style: normal;
  background-repeat: no-repeat;
  background-image: url("../img/svg/location-mark.svg");
}

.tel {
  padding-left: 2rem;
  color: var(--light-color);
  background-repeat: no-repeat;
  background-image: url("../img/svg/tel.svg");
}

.section {
  padding: 1rem;
  @media (min-width: 480px) {
    padding-bottom: 2rem;
  }
  @media (min-width: 1024px) {
    & .subtitle {
      margin-bottom: 50px;
    }
  }
  @media (min-width: 1200px) {
    margin-inline: auto;
    padding: 0 1rem;
    max-width: 1332px;
    & .subtitle {
      margin-bottom: 90px;
    }
  }
  &--contacts {
    padding-bottom: 0;
  }
}

.section--about {
  @media (min-width: 768px) {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-areas: "s s s s s s s s" "t t t x x x x x";
  }
  @media (min-width: 1024px) {
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    grid-template-areas: "s s s s s s s s s s" ". t t t x x x x x ." "v v v v v v v v v v";
    padding-bottom: 50px;
  }
  @media (min-width: 1200px) {
    display: grid;
    grid-template-columns: 2fr 3fr 5fr 2fr;
    grid-template-areas: "s s s s" ". t x .";
    column-gap: 24px;
    padding-bottom: 140px;
    // max-width: calc(1296px + 1rem);
  }
  & .subtitle {
    grid-area: s;
  }
  & .title {
    grid-area: t;
  }
  & .text {
    grid-area: x;
  }
}

.section--menu {
  @media (min-width: 768px) {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-areas: "s s s s s s s s" "t t t x x x x x" "v v v v v v v v";
  }
  @media (min-width: 1024px) {
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    grid-template-areas: "s s s s s s s s s s" ". t t t x x x x x ." "v v v v v v v v v v";
  }
  @media (min-width: 1200px) {
    display: grid;
    grid-template-columns: 2fr 3fr 5fr 2fr;
    grid-template-areas: "s s s s" ". t x ." ". v v .";
    padding-bottom: 140px;
  }
  & .subtitle {
    grid-area: s;
  }
  & .title {
    grid-area: t;
  }
  & .text {
    grid-area: x;
    @media (min-width: 1024px) {
      margin-bottom: 30px;
    }
    @media (min-width: 1200px) {
      margin-bottom: 80px;
    }
  }
  & .visual-blocks {
    grid-area: v;
  }
}

.section--delivery {
  @media (min-width: 768px) {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-areas: "s s s s s s s s" "t t t x x x x x" ". . . v v v v v";
  }
  @media (min-width: 1024px) {
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    grid-template-areas: "s s s s s s s s s s" ". t t t x x x x x ." ". . . . v v v v v .";
    padding-bottom: 50px;
  }
  @media (min-width: 1200px) {
    display: grid;
    grid-template-columns: 2fr 3fr 5fr 2fr;
    grid-template-areas: "s s s s" ". t x ." ". . v .";
    padding-bottom: 140px;
  }
  & .subtitle {
    grid-area: s;
  }
  & .title {
    grid-area: t;
  }
  & .text {
    grid-area: x;
    @media (min-width: 1200px) {
      margin-bottom: 60px;
    }
  }
  & .delivery-buttons {
    grid-area: v;
  }
}

.section--contacts {
  @media (min-width: 768px) {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-areas: "s s s s s s s s" ". i i i i i i ." ". c c c c c c .";
    padding-bottom: 2rem;
  }
  @media (min-width: 1024px) {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-areas: "s s s s s s s s" "i i i i c c c c";
    padding-bottom: 2rem;
  }
  @media (min-width: 1200px) {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-areas: "s s s" "i c .";
    gap: 24px;
    padding-bottom: 140px;
    background-repeat: no-repeat;
    background-image: url("../img/contacts-bg.jpg");
    background-position: right bottom 100px;
  }
  & .subtitle {
    grid-area: s;
    @media (min-width: 1200px) {
      margin-bottom: 140px;
    }
  }
  & .info {
    grid-area: i;
  }
  & .contacts {
    grid-area: c;
  }
}

// burger
.burger {
  cursor: pointer;
  @media (min-width: 768px) {
    display: none;
  }
}

.menu-active {
  display: block !important;
}

// texts
.bold {
  font-weight: 700;
}

.title {
  margin: 0;
  margin-bottom: 1rem;
  font-size: clamp(2rem, 7vw, 64px);
  font-weight: 700;
}

.subtitle {
  display: flex;
  align-items: center;
  gap: 30px;
  margin: 0;
  margin-bottom: 1rem;
  font-size: 20px;
  font-weight: 700;
  text-transform: uppercase;
  &::after {
    content: "";
    display: block;
    width: 60px;
    height: 3px;
    background-color: var(--accent-color);
  }
}

.text {
  margin-bottom: 1rem;
  &:last-child {
    margin-bottom: 0;
  }
}

.text--delivery {
  @media (min-width: 1024px) {
    margin-bottom: 30px;
  }
}

.text__item {
  margin: 0;
  margin-bottom: 1rem;
  line-height: 140%;
  &:last-child {
    margin-bottom: 0;
  }
}

// socials
.socials {
  display: flex;
  justify-content: center;
  gap: var(--var-offset);
  @media (min-width: 768px) {
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    gap: 20px;
  }
  & .social {
    display: block;
    width: 40px;
    height: 40px;
    background-repeat: no-repeat;
    transition: transform .3s ease-in-out;
    &:nth-child(1) {
      background-image: url("../img/svg/youtube-logo.svg");
    }
    &:nth-child(2) {
      background-image: url("../img/svg/vk-logo.svg");
    }
    &:nth-child(3) {
      background-image: url("../img/svg/telegram-logo.svg");
    }
    &:hover {
      transform: translateY(-.25rem);
    }
  }
}

// images
.visual-blocks {
  display: flex;
  flex-direction: column;
  align-items: center;
  @media (min-width: 768px) {
    flex-direction: row;
    justify-content: center;
    gap: 1rem;
  }
  @media (min-width: 1200px) {
    flex-direction: row;
    justify-content: space-between;
    gap: 1rem;
  }
}

.visual-block {
  padding-inline: 1rem;
  max-width: 416px;
  @media (min-width: 1200px) {
    padding-inline: 40px;
  }
}

.visual-block__img {
  position: relative;
  margin-bottom: 2rem;
  @media (min-width: 1200px) {
    margin-bottom: 60px;
  }
  & img {
    position: relative;
    top: 1rem;
    display: block;
    width: 100%;
    @media (min-width: 1200px) {
      top: 40px;
    }
  }
  &:before {
    content: "";
    display: block;
    margin-inline: -1rem;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    border: 1px solid rgba(51, 51, 51, 0.2);
    @media (min-width: 1200px) {
      margin-inline: -40px;
    }
  }
}

// buttons
.btn {
  padding: 15px 30px;
  border: 1px solid rgba(51, 51, 51, 0.2);
  font-size: 20px;
  font-weight: 700;
  line-height: 1;
  color: var(--dark-color);
  background-color: var(--light-color);
  transition: transform .3s ease-in-out;
  &:hover {
    transform: translateY(-.25rem);
  }
}

.btn--small {
  padding: 10px 20px;
  font-size: 16px;
}

.btn--dark {
  border: 1px solid var(--accent-color);
  color: var(--light-color);
  background-color: var(--accent-color);
}

.btn--negative {
  border: 1px solid var(--light-color);
  color: var(--light-color);
  background-color: transparent;
}

.delivery-buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  @media (min-width: 480px) {
    flex-direction: row;
    gap: 30px;
  }
}

.contacts-buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

// blocks
.info {
  margin: 1rem;
  &__list {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    @media (min-width: 480px) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    @media (min-width: 480px) {
      align-items: flex-start;
    }
    &>* {
      display: block;
    }
  }
  &__time {
    font-size: 32px;
    font-weight: 700;
    color: var(--accent-color);
  }
  &__num {
    font-size: 64px;
  }
}

.contacts {
  margin: 0 -1rem;
  padding: 2rem;
  color: var(--light-color);
  background: #5B5B5B;
  @media (min-width: 1024px) {
    margin-inline: 0;
  }
  & a {
    display: block;
    margin-bottom: 1rem;
  }
  &__title {
    font-size: 20px;
  }
}

.footer {
  padding-block: 2rem;
  background-color: var(--dark-color);
  color: var(--light-color);
  &__container {
    display: flex;
    flex-direction: column;
    place-items: center;
    gap: 1rem;
    max-width: 1296px;
    @media (min-width: 1024px) {
      flex-direction: row;
      justify-content: space-between;
      padding-inline: 1rem;
    }
  }
  &__confidence {
    color: var(--light-color);
  }
  &__site-dev {
    color: var(--light-color);
  }
}
