.header {
  padding: .75rem;
  background-color: var(--dark-color);
  display: grid;
  grid-template-areas: "logo ... burger" "socials socials socials";
  @media (min-width: 480px) {
    grid-template-areas: "logo socials burger";
  }
  @media (min-width: 768px) {
    position: absolute;
    grid-template-columns: 120px 1fr;
    grid-template-areas: "logo nav" "socials ...";
    width: 100%;
    height: 500px;
    padding: 0;
    background-color: unset;
  }
  @media (min-width: 1024px) {
    position: absolute;
    grid-template-columns: 120px 1fr;
    grid-template-areas: "logo nav" "socials ...";
    width: 100%;
    height: 750px;
    padding: 0;
    background-color: unset;
  }
  @media (min-width: 1200px) {
    position: absolute;
    grid-template-columns: 120px 1fr;
    grid-template-areas: "logo nav" "socials ...";
    width: 100%;
    height: 1000px;
    padding: 0;
    background-color: unset;
  }
  &__logo {
    grid-area: logo;
    @media (min-width: 768px) {
      padding-top: 30px;
      width: 120px;
      background-color: var(--dark-color);
    }
    & a {
      @media (min-width: 768px) {
        display: block;
        text-align: center;
      }
    }
  }
  &__nav {
    grid-area: nav;
    justify-self: end;
    @media (min-width: 768px) {
      padding-top: 30px;
    }
  }
  &__burger {
    grid-area: burger;
    justify-self: end;
  }
  &__socials {
    grid-area: socials;
    justify-self: center;
    @media (min-width: 768px) {
      padding-bottom: 30px;
      width: 120px;
      background-color: var(--dark-color);
    }
  }
}

.nav-header {
  display: none;
  margin-right: 1rem;
  @media (min-width: 768px) {
    display: block;
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 50px;
  }
  &__link {
    display: block;
    font-size: 16px;
    color: var(--light-color);
    &--featured {
      padding: 10px 20px;
      background-color: var(--accent-color);
    }
  }
}

.header--smallmenu {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  inset: 0 0 0 0;
  height: 100vh;
  padding: 1rem 0;
  & .header__logo {
    order: -1;
  }
  & .header__burger {
    order: -1;
  }
  & .nav-header {
    flex-grow: 1;
    margin-right: 0;
  }
  & .nav-header__link {
    font-size: 32px;
    font-weight: 700;
    &--featured {
      font-size: 21px;
    }
  }
  & .nav-header__list {
    flex-direction: column;
    gap: 2rem;
  }
}
