.hero {
  display: flex;
  place-items: center;
  height: 18rem;
  padding: 1rem;
  background-repeat: no-repeat;
  background-image: url("../img/restorant-food.jpg");
  background-size: cover;
  background-position: center center;
  @media (min-width: 768px) {
    align-items: flex-end;
    margin-bottom: 2rem;
    padding-bottom: 70px;
    padding-left: 150px;
    height: 500px;
  }
  @media (min-width: 1024px) {
    align-items: flex-end;
    margin-bottom: 2rem;
    padding-bottom: 150px;
    padding-left: 150px;
    height: 750px;
  }
  @media (min-width: 1200px) {
    align-items: flex-end;
    margin-bottom: 153px;
    padding-bottom: 150px;
    padding-left: 150px;
    height: 1000px;
  }
  &__title {
    margin: 0;
    font-family: var(--font-family);
    font-size: clamp(1.75rem, 7vw, 54px);
    font-weight: 700;
    text-align: center;
    color: var(--light-color);
  }
}
